<template>
  <v-container fluid>
    <v-row justify="center">
      <v-col
        cols="12"
        md="6"
      >
        <material-card
          :loading="formLoading"
          class="v-card-profile"
          color="primary"
        >
          <template v-slot:header>
            <div
              class="pa-3"
            >
              <h4
                class="title font-weight-light"
              >
                Form Schemas
              </h4>
            </div>

            <v-spacer />
            <v-btn
              color="secondary"
              class="mr-3"
              @click="() => addSchema('form')"
            >
              <v-icon class="pr-3">
                mdi-plus
              </v-icon>Add Schema
            </v-btn>
          </template>
          <profile-list
            :items="formSchemas"
            @delete="(schema) => onSchemaDelete(schema, 'form')"
          />
        </material-card>
      </v-col>
      <v-col
        cols="12"
        md="6"
      >
        <material-card
          :loading="contentLoading"
          class="v-card-profile"
          color="primary"
        >
          <template v-slot:header>
            <div
              class="pa-3"
            >
              <h4
                class="title font-weight-light"
              >
                Content Schemas
              </h4>
            </div>

            <v-spacer />
            <v-btn
              color="secondary"
              class="mr-3"
              @click="() => addSchema('content')"
            >
              <v-icon class="pr-3">
                mdi-plus
              </v-icon>Add Schema
            </v-btn>
          </template>
          <profile-list
            :items="contentSchemas"
            @delete="(schema) => onSchemaDelete(schema, 'content')"
          />
        </material-card>
      </v-col>
    </v-row>
    <v-dialog
      v-model="dialog"
      scrollable
      width="500"
    >
      <material-card title="Add Schema">
        <v-row>
          <v-text-field
            v-model="name"
            label="Schema Name"
          />
        </v-row>
        <v-row>
          <v-text-field
            v-model="notification"
            label="Notification"
          />
        </v-row>
        <template v-for="(field,i ) in fields">
          <v-row
            :key="i"
            align="center"
            class="mb-2"
            :style="'border: 1px solid; border-color: ' + $vuetify.theme.themes.dark.primary"
          >
            <v-col cols="8">
              <h6 class="overline">
                Field {{ i + 1 }}
              </h6>
            </v-col>
            <v-col
              cols="4"
              class="text-right"
            >
              <v-btn
                icon
                @click="() => removeField(i)"
              >
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </v-col>
            <v-col
              class="py-0"
              cols="12"
            >
              <v-text-field
                v-model="field.name"
                label="Name"
              >
                <template v-slot:append-outer>
                  <v-checkbox
                    v-model="field.required"
                    class="mt-0"
                    label="Required"
                  />
                </template>
              </v-text-field>
            </v-col>
            <v-col
              class="py-0"
              cols="12"
            >
              <v-text-field
                v-model="field.type"
                label="Type"
              >
                <template v-slot:append-outer>
                  <v-checkbox
                    v-model="field.unique"
                    class="mt-0"
                    label="Unique"
                  />
                </template>
              </v-text-field>
            </v-col>
          </v-row>
        </template>
        <v-row class="mt-4">
          <v-btn
            color="primary"
            class="mr-3"
            block
            @click="addField"
          >
            <v-icon class="pr-3">
              mdi-plus
            </v-icon>Add Field
          </v-btn>
        </v-row>
        <template v-slot:actions>
          <v-spacer />
          <v-btn
            color="primary"
            :loading="uploadingSchema"
            @click="uploadSchema"
          >
            Add Schema
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
        </template>
      </material-card>
    </v-dialog>
    <yes-no-dialog
      ref="yesno"
      @yes="currentDelete"
    />
  </v-container>
</template>

<script>
  import moment from 'moment'
  import ProfileList from '../components/core/ProfileList.vue'
  import YesNoDialog from '../components/core/YesNoDialog.vue'
  export default {
    components: {
      ProfileList,
      YesNoDialog
    },
    data () {
      return {
        name: '',
        notification: '',
        formSchemas: [],
        contentSchemas: [],
        formLoading: false,
        contentLoading: false,
        uploadingSchema: false,
        fields: [{
          required: true
        }],
        currentType: '',
        dialog: false,
        currentDelete: () => {},
      }
    },
    computed: {
      site: function () {
        return this.$store.state.site
      }
    },
    mounted () {
      this.getSchemas(['form', 'content'])
    },
    methods: {
      moment,
      addSchema (schemaType) {
        this.currentType = schemaType
        this.dialog = true
      },
      addField () {
        this.fields.push({
          required: true
        })
      },
      removeField (i) {
        this.fields.splice(i, 1)
      },
      uploadSchema () {
        this.uploadingSchema = true

        var schema = {
          siteId: this.site,
          name: this.name,
          notification: this.notification,
          fields: this.fields
        }

        this.api.post(this.currentType + '/schemas', schema)
          .then(result => {
            this.fields = [{
              required: true
            }]
            this.name = ''
            this.getSchemas([this.currentType])

            this.uploadingSchema = false
            this.dialog = false
          }, error => {
            this.uploadingSchema = false
            this.error = error
          })
      },
      getSchemas (schemaTypes) {
        schemaTypes.forEach(schemaType => {
          this[schemaType + 'Loading'] = true
          this.api.get(schemaType + '/schemas/' + this.site)
            .then(result => {
              var schemas = result.data
              schemas.forEach(schema => {
                schema.sub1 = schema.schemaId
                schema.sub2 = this.moment(schema.created).format('DD-MM-YY')
              })
              this[schemaType + 'Schemas'] = schemas
              this[schemaType + 'Loading'] = false
            }, error => {
              this[schemaType + 'Loading'] = false
              this.error = error
            })
        })
      },
      deleteSchema (schema, schemaType, done) {
        this.api.delete(schemaType + '/schemas/' + schema.schemaId)
          .then(result => {
            this.getSchemas([schemaType])
            done()
          }, error => {
            done()
            this.error = error
          })
      },
      onSchemaDelete (schema, schemaType) {
        this.currentDelete = (done) => this.deleteSchema(schema, schemaType, done)
        this.$refs.yesno.open(`Delete ${schema.name}`, `Are you sure you want to delete the ${schemaType} schema called ${schema.name}?`)
      },
    }

  }
</script>
